<template>
	<view class="news-container">
		<view class="search-bar">
			<view class="search-box">
				<uni-icons type="search" size="18" color="#999" />
				<input class="search-input" placeholder="请输入关键词搜索" placeholder-class="placeholder"
					@input="onSearchInput" />
			</view>
		</view>

		<scroll-view class="category-nav" scroll-x>
			<view v-for="(item, index) in categories" :key="item.id" class="category-item"
				:class="{ active: activeCategory === index }" @click="switchCategory(index)">
				{{ item.name }}
				<view v-if="activeCategory === index" class="indicator"></view>
			</view>
		</scroll-view>

		<scroll-view class="news-list" scroll-y :style="{ height: scrollHeight + 'px' }">
			<view v-for="news in filteredNews" :key="news.id" class="news-item" @click="navigateToDetail(news)">
				<image class="news-image" :src="news.cover ? news.cover :'https://img0.baidu.com/it/u=3524251599,1183611687&fm=253&fmt=auto&app=138&f=GIF?w=285&h=285'"  mode="aspectFill" />
				<view class="news-content">
					<text class="news-title">{{ news.title }}</text>
					<text class="news-summary">{{ news.description }}</text>
					<view class="news-meta">
						<text class="meta-item">{{ initTime(news.createtime) }}</text>
						<view class="meta-item">
							<uni-icons type="hand-up" size="14" />
							<text>{{ news.likeCount }}</text>
						</view>
					</view>
				</view>
			</view>
		</scroll-view>
	</view>
</template>

<script>
	import {api} from '@/api/index.js'
	import {myMixin} from '@/utils/public.js'
	export default {
		mixins:[myMixin],
		data() {
			return {
				activeCategory: 0,
				searchText: '',
				scrollHeight: 0,
				categories: [{
						id: 0,
						name: '推荐'
					},
					{
						id: 1,
						name: '热点'
					},
					{
						id: 2,
						name: '科技'
					},
					{
						id: 3,
						name: '财经'
					},
					{
						id: 4,
						name: '体育'
					},
					{
						id: 5,
						name: '娱乐'
					}
				],
				query:{
					page:1,
					limit:10
				},
				newsList: [
					// {
					// 	id: 1,
					// 	cover: 'https://p3-sign.toutiaoimg.com/tos-cn-i-tjoges91tu/6364552d41c8fa05f6f4eaa18ffbb644~tplv-tt-cs0:1920:1080.jpg?_iz=31826&bid=11&from=article.headline&gid=7494028106890478119&lk3s=06827d14&x-expires=1747962193&x-signature=UfX9dikcqRW%2FsVB8h8HqhEx87to%3D',
					// 	title: '人工智能新突破：GPT-4正式发布',
					// 	likeCount: 0,
					// 	category: 2
					// },

				]
			}
		},
		computed: {
			filteredNews() {
				return this.newsList.filter(item => {
					const matchCategory = this.activeCategory === 0 ||
						item.category === this.activeCategory
					const matchSearch = item.title.includes(this.searchText) ||
						item.summary.includes(this.searchText)
					return matchCategory && matchSearch
				})
			}
		},
		mounted() {
			this.calcScrollHeight()
			this.getData()
		},
		methods: {
			async getData(){
				let data = await api.informationData(this.query)
				if (data.code == 200){
					console.log(data.data.listdata)
					this.newsList = [...this.newsList,...data.data.listdata]
				}
			},
			calcScrollHeight() {
				const systemInfo = uni.getSystemInfoSync()
				this.scrollHeight = systemInfo.windowHeight - 180 // 根据实际高度调整
			},
			switchCategory(index) {
				this.activeCategory = index
			},
			onSearchInput(e) {
				this.searchText = e.detail.value.trim()
			},
			navigateToDetail(item) {
				let content = encodeURIComponent(item.content)
				uni.navigateTo({
					url: `/subPackages/shoppingPageList/realTimeInfoDetail/realTimeInfoDetail?content=${content}`
				})

			}
		}
	}
</script>

<style lang="scss" scoped>
	.news-container {
		background: #f5f5f5;
	}

	.search-bar {
		padding: 20rpx 30rpx;
		background: #fff;

		.search-box {
			display: flex;
			align-items: center;
			height: 80rpx;
			padding: 0 30rpx;
			background: #f8f8f8;
			border-radius: 40rpx;

			.search-input {
				flex: 1;
				height: 100%;
				margin-left: 20rpx;
				font-size: 28rpx;
			}

			.placeholder {
				color: #999;
			}
		}
	}

	.category-nav {
		white-space: nowrap;
		background: #fff;
		padding: 20rpx 0;

		.category-item {
			display: inline-block;
			position: relative;
			padding: 0 40rpx;
			font-size: 30rpx;
			color: #666;
			transition: color 0.3s;

			&.active {
				color: #007AFF;
				font-weight: bold;
			}

			.indicator {
				position: absolute;
				bottom: -10rpx;
				left: 50%;
				transform: translateX(-50%);
				width: 40rpx;
				height: 6rpx;
				background: #007AFF;
				border-radius: 3rpx;
			}
		}
	}

	.news-list {
		padding: 30rpx;

		.news-item {
			width: 700rpx;
			display: flex;
			margin-bottom: 30rpx;
			background: #fff;
			border-radius: 16rpx;
			overflow: hidden;
			box-shadow: 0 4rpx 12rpx rgba(0, 0, 0, 0.05);

			.news-image {
				margin: 20rpx;
				width: 250rpx;
				height: 250rpx;
				flex-shrink: 0;
			}

			.news-content {
				flex: 1;
				padding: 20rpx;
				display: flex;
				flex-direction: column;

				.news-title {
					font-size: 32rpx;
					color: #333;
					line-height: 1.4;
				}

				.news-summary {
					margin-top: 16rpx;
					font-size: 26rpx;
					color: #666;
				}

				.news-meta {
					margin-top: auto;
					display: flex;
					align-items: center;
					font-size: 24rpx;
					color: #999;

					.meta-item {
						margin-right: 30rpx;
						display: flex;
						align-items: center;

						uni-icons {
							margin-right: 8rpx;
						}
					}
				}
			}
		}
	}

	// 文本溢出处理
	@mixin text-overflow($line: 1) {
		@if $line ==1 {
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap;
		}

		@else {
			display: -webkit-box;
			-webkit-box-orient: vertical;
			-webkit-line-clamp: $line;
			overflow: hidden;
		}
	}
</style>